import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Home from '@/components/Home'
import Dashboard from '@/components/Dashboard'
import firebase from '../Firebase'

Vue.use(Router)

async function getUser() {
    const auth_id = localStorage.getItem('UID');
    const user = await firebase.firestore().collection('users').where("auth_id", "==", auth_id).get();
    return user.docs.map(doc => doc.data());
}

const routes = [{
    path: '/',
    name: 'Home',
    component: Home
}, {
    path: '/login',
    name: 'Login',
    component: Login
}, {
    path: '/Dashboard',
    name: 'Dashboard',
    component: Dashboard,
    beforeEnter: (to, from, next) => {
        getUser().then(user => {
            if (user.length === 0 || user.length === undefined) {
                next({name: 'Login'})
            } else {
                next();
            }
        })
    }
}]

export default new Router({
    mode: "history",
    routes
})