<template>
    <div class="wrapper">
        <div class="toast toast-success" v-if="success">Added Success</div>
        <div class="toast toast-failure" v-if="failure">Could Not Add Lecture! Check Console</div>
        <div class="invitation">
            <textarea
                @change="updateValue"
                v-model="invitation"
                name="zoom"
                id="zoomLink"
                cols="30"
                rows="28"
            >
            </textarea>
        </div>
        <form @submit.prevent="saveLecture" class="data">
            <span>
                <label for="link">JOINING LINK</label>
                <input
                    required="true"
                    v-model="lecture.link"
                    name="link"
                    placeholder="Change Link"
                    type="text"
                />
            </span>

            <div class="row">
                <span>
                    <label for="meeting_id">MEETING ID</label>
                    <input
                        required="true"
                        v-model="lecture.meeting_id"
                        name="meeting_id"
                        placeholder="Change Meeting Id"
                        type="text"
                    />
                </span>
                <span>
                    <label for="passcode">PASSWORD</label>
                    <input
                        required="true"
                        v-model="lecture.passcode"
                        name="passcode"
                        placeholder="Change Passcode"
                        type="text"
                    />
                </span>
            </div>
            <span>
                <label for="title">Title</label>
                <input
                    required="true"
                    v-model="lecture.title"
                    name="title"
                    placeholder="Change Title"
                    type="text"
                />
            </span>
            <span>
                <label for="teacher">TEACHER</label>
                <input
                    required="true"
                    type="text"
                    name="teacher"
                    list="teachers"
                    v-model="lecture.teacher"
                    placeholder="Write Teacher Name / Select Teacher"
                />
                <datalist id="teachers">
                    <option>{{ lecture.teacher }}</option>
                    <option>Gauravi Mam</option>
                    <option>Devang Sir</option>
                    <option>Prashant Sir</option>
                    <option>Riddhi Mam</option>
                    <option>Manish Sir</option>
                    <option>Mrunali Mam</option>
                    <option>Vishesh Sir</option>
                    <option>Jayshree Mam</option>
                </datalist>
            </span>
            <span>
                <label for="time">TIME</label>
                <input
                    required="true"
                    v-model="lecture.time"
                    name="time"
                    placeholder="Format : Dec 14, 2020 01:00 PM"
                    type="text"
                />
            </span>
            <button type="submit">Add Lecture</button>
        </form>
    </div>
</template>

<script>
import firebase from "firebase";
import firebaseApp from "../Firebase";

export default {
    name: "Dashboard",
    data() {
        return {
            invitationLink: "",
            lecture: {
                link: "",
                meeting_id: "",
                passcode: "",
                title: "",
                teacher: "",
                time: "",
            },
            success: false,
            failure: false,
        };
    },
    computed:{
      invitation: {
          get: function () {
            if(this.lecture.link == "" && this.lecture.meeting_id == "" && this.lecture.passcode == "" && this.lecture.title == "" && this.lecture.teacher == ""){
                return ""
            }
            else{
                return this.invitationLink;   
            }
          },
          set:function(value){
              this.invitationLink = String(value);
              this.updateValue(String(value));
          },
      }
    },
    methods: {
        updateValue: function (invite_link) {

            if(invite_link.type === 'change'){
                return
            }
            invite_link = String(invite_link);
            if (invite_link ===  "" || invite_link === null) {
                return "";
            } else {
                if(invite_link === null){
                    return "";
                }
                // Joining Link
                var regex = new RegExp(
                    // "https://\\w{0,}.\\w{0,}.\\w{0,}/j.\\w{0,}[\\?]pwd=\\w{0,}",
                    "https://[\\w\\./\\?=]{0,}",
                    "g"
                );
                this.lecture.link = invite_link.match(regex) === null ? "" : invite_link.match(regex)[0];

                // Meeting ID
                regex = new RegExp("\\d{3} \\d{4} \\d{4}", "g");
                this.lecture.meeting_id = invite_link.match(regex) === null ? "" : invite_link.match(regex)[0];
                
                // Passcode
                regex = new RegExp("Passcode: \\w*", "g");
                this.lecture.passcode = invite_link.match(regex) === null ? "" : invite_link.match(regex)[0].substr(10);

                // Title or Topic
                regex = new RegExp("Topic: [a-zA-Z /'.]*", "g");
                this.lecture.title = invite_link.match(regex) === null ? "" : invite_link.match(regex)[0].substr(7);

                // Teacher
                regex = new RegExp("(manish|gauravi|mrunali|vishesh|jayshree|prashant|riddhi)", "gi");
                let condition = invite_link.match(regex) === null ? "" : invite_link.match(regex)[0]; 
                switch (condition) {
                    case "Gauravi":
                        this.lecture.teacher = "Gauravi Mam"
                        break;
                    case "Manish":
                        this.lecture.teacher = "Manish Sir"
                        break;
                    case "Mrunali":
                        this.lecture.teacher = "Mrunali Mam"
                        break;
                    case "Jayshree":
                        this.lecture.teacher = "Jayshree Mam"
                        break;
                    case "Riddhi":
                        this.lecture.teacher = "Riddhi Mam"
                        break;
                    case "Prashant":
                        this.lecture.teacher = "Prashant Sir"
                        break;
                    case "Vishesh":
                        this.lecture.teacher = "Vishesh Sir"
                        break;
                    default:
                        regex = new RegExp(".{1,}(?=[a-zA-Z])", "g");
                        this.lecture.teacher = invite_link.match(regex)[0].split("is") ===  null ? "" : invite_link.match(regex)[0].split("is")[0];
                        break;
                }

                // Time
                regex = new RegExp(
                    "Time:\\ [\\w\\*\\ ,:]{0,}(AM|PM)",
                    "g"
                );
                this.lecture.time = invite_link.match(regex) === null ? "" : invite_link.match(regex)[0].substr(6);
            }
            return
        },
        saveLecture: async function () {
            let lecRef = firebaseApp.firestore().collection("lectures");
            let x = new Date(this.lecture.time);
            let datetime = new firebase.firestore.Timestamp.fromDate(x);
            const lec = {
                link: this.lecture.link,
                meeting_id: this.lecture.meeting_id,
                passcode: this.lecture.passcode,
                teacher: this.lecture.teacher,
                time: datetime,
                title: this.lecture.title,
            };
            lecRef.add(lec).then((docRef) => {
                console.log(docRef);
                this.lecture.link = "";
                this.lecture.meeting_id = "";
                this.lecture.passcode = "";
                this.lecture.teacher = "";
                this.lecture.time = "";
                this.lecture.title = "";
                if(docRef.id){
                    this.success = true;
                    this.failure= false;
                    setTimeout(()=> {this.success = false}, 3000)
                }
                else{
                    this.failure = true;
                    this.success = false;
                    setTimeout(() => {this.failure = false}, 3000)
                }
            });
        },
    },
};
</script>

<style scoped>
*,
*::before,
*::after {
    color: white;
    box-sizing: border-box;
}

span {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-color: rgba(255, 255, 255, 0.05);
    padding: 1.5rem 1rem;
    min-width: 100%;
    border-radius: 12px;
}

label {
    user-select: none;
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.75rem;
    margin-bottom: 0.5rem;
    letter-spacing: 1px;
}

input {
    background-color: transparent !important;
    border: none;
    color: #ffffff;
    font-size: 1rem;
    width: 100%;
}

input[type="password"] {
    letter-spacing: 5px;
}

input:focus,
textarea:focus,
select:focus {
    border: none;
    outline: none;
}

input:focus::placeholder {
    color: transparent;
}

button {
    margin-top: 1rem;
    background-color: transparent;
    border: none;
    background-color: rgba(255, 255, 255, 0.05);
    font-size: 1rem;
    width: 100%;
    padding: 1.5rem 1rem;
    border-radius: 12px;
    align-self: flex-end;
    cursor: pointer;
}

select {
    width: 100%;
    background-color: transparent;
    padding: 1rem 0;
}

select option {
    background-color: rgba(255, 255, 255, 0.5);
}

.wrapper {
    padding: 15vh 5% 5% 5%;
    display: flex;
    width: 100%;
    gap: 1rem;
    background-color: #121212;
}

.invitation {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.invitation textarea {
    background-color: rgba(255, 255, 255, 0.05);
    padding: 2rem 1.25rem;
    border-radius: 12px;
    line-height: 1.5;
    resize: none;
    outline: none;
    border: none;
}

.data {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.data button {
    /* border-radius: 0; */
    transition: all 300ms ease-in;
}

.data button:hover {
    box-shadow: 7px 7px 60px rgba(0, 0, 0, 0.35);
    background-color: rgb(32, 209, 141);
    color: #121212;
}

.row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1em;
}

.toast{
    background-color: #2e2e2e;
    padding: 1rem 3rem;
    position: absolute;
    left: 5%;
    bottom: 5%;
    border-radius: 0.5em;
    animation: toast-up 3000ms ease-in-out;
}

.toast-success{
    border-left: 5px solid rgb(0, 255, 110);
}
.toast-failure{
    border-left: 5px solid rgb(255, 72, 0);
}

@media only screen and (max-width: 600px) {
    .wrapper {
        flex-direction: column;
    }
}

@keyframes toast-up {
    0%{
        bottom: 0%;
    }
    20%{
        bottom: 5%;
    }
    75%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
</style>