<template>
<div>
  <div>
    <form method="POST" @submit.prevent="signIn">
      <span>
        <label for="email">EMAIL</label>
        <input v-model="email" name="email" placeholder="example@service.com" type="email">
      </span>
      <span>
        <label for="password">PASSWORD</label>
        <input v-model="password" name="password" placeholder="•••pwd•••" type="password">
      </span>
      <button type="submit">Login</button>
    <div class="toast" v-if="error.code !==''">
      {{error.message}}
    </div>
    </form>
  </div>
</div>
</template>

<script>
import firebase from '../Firebase';

export default {
  name: 'Login',
  data(){
    return{
      email: "",
      password: "",
      error: {code: "",message:""}
    }
  },
  methods:{
    signIn : function(){
      firebase.auth().signInWithEmailAndPassword(this.email, this.password)
        .then((user) => {
          console.log(user);
          localStorage.setItem('UID', user.  user.uid);
          this.$router.push('dashboard')
        })
        .catch((error) => {
          console.error(error)
          this.error.code = error.code;
          this.error.message = error.message;
          setTimeout(()=>{
            this.error.code = ""
            this.error.message= ""
          }, 3000)
        });

    },
    getUser: async function(){
			const auth_id = localStorage.getItem('UID');
			const user = await firebase.firestore().collection('users').where("auth_id", "==", auth_id).get();
			return user.docs.map(doc => doc.data());
		},
  },
  created: async function(){
    const user = await this.getUser().then(doc => {return doc})
    if(user.length === 0 || user.length === undefined){
      console.log("Not A Verified User")
    }
    else{
      this.$router.push('dashboard')
    }
  }
}
</script>

<style scoped>

*{
  font-family: 'DM Sans', sans-serif;
  color: white;
}

div{
  width: 100%;
  height: 100vh;
  background-color: #121212;
  display: flex;
  justify-content: center;
  align-items: center;
}

form{
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 33%;
}

span{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: rgba(255, 255, 255, 0.05);
  padding: 1.5rem 1rem;
  min-width: 100%;
  border-radius: 12px;
}

label{
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
  margin-bottom: 0.5rem;
  letter-spacing: 1px;  
}

input{
  background-color: transparent !important;
  border: none;
  color: #ffffff;
  font-size: 1rem;
  width: 100%;
}

input[type=password]{
  letter-spacing: 5px;
}

input:focus{
  border: none;
  outline: none;
}

input:focus::placeholder{
  color: transparent;
}

button{
  margin-top: 1rem;
  background-color: transparent;
  border: none;
  background-color:rgba(255, 255, 255, 0.05);
  font-size: 1rem;
  width: 100%;
  padding: 1.5rem 1rem;
  border-radius: 12px;
  align-self: flex-end;
  cursor: pointer;
}

.toast{
  background-color: rgba(255, 38, 0, 0.205);
  height: auto;
  padding: 1.5rem;
  border-radius: 12px;
}

</style>