<template>
  <a :href="link" class="card">
    <div class="title">
        <h3>{{title}}</h3>
        <h5>{{teacher}}</h5>
    </div>
    <div class="info">
    <h5>{{datetime(time)}}</h5>
    <svg class="arrow" width="61" height="8" viewBox="0 0 61 8" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M60.8536 4.35355C61.0488 4.15829 61.0488 3.84171 60.8536 3.64645L57.6716 0.464466C57.4763 0.269204 57.1597 0.269204 56.9645 0.464466C56.7692 0.659728 56.7692 0.976311 56.9645 1.17157L59.7929 4L56.9645 6.82843C56.7692 7.02369 56.7692 7.34027 56.9645 7.53553C57.1597 7.7308 57.4763 7.7308 57.6716 7.53553L60.8536 4.35355ZM0 4.5H60.5V3.5H0V4.5Z" fill="white"/>
    </svg>
    </div>
  </a>
</template>

<script>
export default {
    name: 'LectureCards',
    props: ['title', 'teacher', 'link', 'meeting_id', 'time', 'passcode'],
    methods: {
        datetime: function(time){

            let month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]; 

            let dateString = new Date(time.seconds * 1000); 
            let timeString = String(dateString.toLocaleTimeString());
            let meridiem = timeString.match("AM") ? "AM" : "PM"
            var regex = new RegExp("\\d{0,2}:", "g");
            timeString = timeString.match(regex).join('').slice(0,-1)
            let cardString = month[dateString.getMonth()] + " " + String(dateString.getDate()) + " " + String(dateString.getFullYear()) + "  •  " + timeString + " " + meridiem;
            return cardString
        }
    }
}
</script>

<style scoped>

*{
    color: #fff;
    text-align: left;
}

a{
    text-decoration: none;
}

.card{
    border-right: 1px solid #ffffff23;
    border-bottom: 1px solid #ffffff41;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 4rem 5rem;
    background-color: #000000;
    width: auto;
}

.title{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 2rem;
}

.card .title h3{
    font-family: 'Darker Grotesque', sans-serif;
    font-size: 1.875rem;
    font-weight: 600;
    margin-bottom: 0.3em;
}

.info{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.info h5{
    font-family: 'Darker Grotesque';
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

h5{
    font-family: 'DM Sans', sans-serif;
    font-weight: 400;
}

@media only screen and (max-width: 600px) {
    .card{
        padding: 3rem 2rem;
    }
}

</style>