<template>
    <div>
        <div class="hero">
            <h1>
                Click On Cards To Start Lecture. <br />
                This Is Maintained By Students. 
            </h1>
        </div>
        <div class="lectures">
            <LectureCards
                v-for="(lecture, index) in lectures"
                :key="index"
                :title="lecture.title"
                :teacher="lecture.teacher"
                :link="lecture.link"
                :time="lecture.time"
                :meeting_id="lecture.meeting_id"
                :passcode="lecture.passcode"
            />
        </div>
    </div>
</template>

<script>
import LectureCards from "./LectureCards.vue";
import firebase from "firebase";

export default {
    name: "Home",
    components: { LectureCards: LectureCards },
        data() {
        return {
            lectures: [],
        };
    },
    created: function () {
        // firebase.firestore().collection('lectures').get().then((snapshot) => {
        //     snapshot.forEach((doc) => {
        //         this.lectures.push(doc);
        //     });
        // });
        // console.log(this.lectures)
        // let unsubscribe = firebase.firestore().collection('lectures').onSnapshot((snapshot) => {
        //     snapshot.docChanges().forEach((change) => {
        //         if (change.type === "added") {
        //             this.lectures.unshift(change.doc.data());
        //             let newLec = JSON.parse(localStorage.getItem('lectures')).unshift(change.doc.data());
        //             localStorage.setItem("lectures", JSON.stringify(newLec))
        //         }
        //     });
        // });

        let lastFetch = localStorage.getItem("lastFetch");
        if (Number(new Date().getTime()) < Number(lastFetch) + 600000) {
            console.log("Can't Fetch Within 10 Minutes of Last Fetch");
            this.lectures = JSON.parse(localStorage.getItem("lectures"));
        } else {
            this.getLectures().then((doc) => {
                console.log("fetching lectures...");
                localStorage.setItem("lastFetch", new Date().getTime());
                localStorage.setItem("lectures", JSON.stringify(doc));
                this.lectures = doc;
            });
        
        }
    },
    methods: {
        getLectures: async function () {
            const snapshot = await firebase
                .firestore()
                .collection("lectures")
                .orderBy("time")
                .get();
            return snapshot.docs.map((doc) => doc.data());
        },
    },
};
</script>

<style scoped>
h1 {
    text-align: left;
    font-size: 3.125rem;
    color: white;
    font-weight: 500;
    -webkit-font-smoothing: antialiased;
}

.hero {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 60vh;
    padding: 0 5% 2.5% 5%;
}

.lectures {
    background-color: #000000;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(33%, 1fr));
}

@media only screen and (max-width: 1024px) {
    .lectures {
        grid-template-columns: repeat(auto-fit, minmax(50%, 1fr));
    }
}
@media only screen and (max-width: 600px) {
    .hero {
        height: 60vh;
    }
    .hero h1 {
        margin-top: 3rem;
        font-size: 2rem;
    }
    .lectures {
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
    }
}
</style>
        